.toggle-container {
  width: 60px;
  background-color: #3fd157;
  cursor: pointer;
  -webkit-user-select: none;
  user-select: none;
  border-radius: 3px;
  padding: 2px;
  height: 30px;
  position: relative;
  border-radius: 32px;
}

.toggle-container.off {
  background: linear-gradient(283.7deg, #e8e8e8 0%, #f4f4f4 100%);
}

.dialog-button {
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 20px;
  min-width: unset;
  box-sizing: border-box;
  position: absolute;
  left: 33px;
  transition: all 0.3s ease;
  padding: 12px;
  border-radius: 32px;
  top: 3px;
  background: #ffffff;
  /*box-shadow: 0px 4px 4px #AEAEAE;*/
}

.disabled {
  background: #ffffff;
